
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import tools from '../../lib/tools.js';
export default {
name: 'serviceReport',
components: { Flexbox, FlexboxItem,Toast},
data () {
return {
showToast:false,
toastMsg:'',
serviceReport:[]
}
},
created(){
this.loading();
},
methods:{
loading(){
var vm = this;
let orderId = tools.getRequestParam('orderId');
let pageSize=1000,pageNum=1;
let data = {"showCount": pageSize,"currentPage": pageNum,"orderId":orderId};
this.$http.axios({method:'post',url:'/orderInfo/getReportArray',data}).then((result) => {
let data = result.data;
console.log(JSON.stringify(result))
console.log(JSON.stringify(result.data));
if(data.successful && (data.status==200)){
if(data.items){
vm.serviceReport = data.items;
console.log(vm.serviceReport);
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
});
this.toastMsg = '';
}
}
}



